import { NgModule } from '@angular/core';
import { CommonModule, CoreModule } from '@c8y/ngx-components';
import { HOOK_DEVICE_REGISTRATION} from '@c8y/ngx-components/register-device';
import { ActilityDeviceRegistrationButtonComponent } from './actility-device-registration-button.component';
import { ActilityDeviceRegistrationFactory } from './actility-device-registration.factory';
import { ActilityDeviceRegistrationComponent } from './actility-device-registration.component';
import { ActilityDeviceRegistrationService } from './actility-device-registration.service';

@NgModule({
  imports: [CoreModule, CommonModule],
  declarations: [ActilityDeviceRegistrationButtonComponent, ActilityDeviceRegistrationComponent],
  providers: [
    ActilityDeviceRegistrationService,
    {
      provide: HOOK_DEVICE_REGISTRATION,
      useClass: ActilityDeviceRegistrationFactory,
      multi: true
    }
  ]
})
export class ActilityDeviceRegistrationModule {}
